* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.submit-button {
  background-color: #4CAF50;
  /* Couleur de fond verte */
  color: white;
  /* Couleur du texte */
  border: none;
  /* Pas de bordure */
  padding: 10px 20px;
  /* Espacement interne */
  border-radius: 5px;
  /* Bords arrondis */
  cursor: pointer;
  /* Curseur pointeur */
  font-size: 16px;
  /* Taille de la police */
  transition: background-color 0.3s ease;
  /* Effet de transition */
  margin-left: 10px;
  /* Espacement à gauche */
}

.submit-button:hover {
  background-color: #45a049;
  /* Couleur de fond au survol */
}

/* Styles généraux */
body,
html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 10px;
  background-color: #f4f4f4;
}

.Nexus-container {
  max-width: 400px;
  /* Limite la largeur sur les écrans larges */
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  padding: 20px;
  text-align: center;
}

/* Logo Nexus */
.Nexus-image {
  width: 80px;
  height: auto;
  margin-bottom: 20px;
}

/* Styles pour la barre de recherche */
.search-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-search-element {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
  transition: all 0.3s ease;
}

.input-search-element:focus {
  outline: none;
  border-color: #007bff;
  box-shadow: 0px 0px 5px rgba(0, 123, 255, 0.5);
}

.submit-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #0056b3;
}

/* Liste des suggestions */
.Nexus-search-items {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 150px;
  /* Limite la hauteur de la liste */
  overflow-y: auto;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.Nexus-search-items li {
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.Nexus-search-items li:hover {
  background-color: #f0f0f0;
}

/* Logo Nexus */
.Nexus-image {
  width: 950px;
  /* Ajuste cette valeur pour une taille par défaut */
  height: auto;
  /* Maintient les proportions */
  max-width: 100%;
  /* La rend responsive sur petits écrans */
  margin-bottom: 20px;
}

/* Style pour mobile */
@media (max-width: 900px) {
  .Nexus-container {
    padding: 90px;
  }

  .input-search-element,
  .submit-button {
    font-size: 14px;
    padding: 40px;

  }





  .submit-button {
    padding: 10px;
  }
}